

export class FormValidator {

    constructor(value) {
        this.value = value;
    }

    requiredValidateError() {
        return this.value ? null : ',   '
    }

    regexValidateError() {
        const regex = /^[A-Za-z--0-9\s]*$/;
        return regex.test(this.value) ? null : ',     (   )'
    }
}